html {
  box-sizing: border-box;
}

*,
::after,
::before {
  box-sizing: inherit;
}

body {
  font-family: 'Lato', sans-serif;
  background-color: #eaeaea;
}
.form {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
    border-bottom: solid 10px #eaeaea;
    padding: 20px;
    grid-gap: 20px;
    gap: 20px;
}

.form__input {
    border: 3px solid #eaeaea;
    transition: 100ms;
    flex-grow: 1;
    padding: 15px;
}

.form__input:focus {
    transition: 100ms;
    -webkit-transform: scale(103%);
            transform: scale(103%);
}

.form__button {
    background-color: teal;
    color: white;
    border: none;
    transition: 100ms;
    font-size: large;
    font-weight: 700;
    padding: 15px;
}

.form__button:hover {
    transition: 100ms;
    -webkit-transform: scale(105%);
            transform: scale(105%);
    background-color: hsl(180, 100%, 30%);
}

@media (max-width: 767px) {
    .form__input {
        flex-basis: 100%;
    }

    .form__button {
        flex-basis: 100%;
    }
}
.list {
    list-style: none;
    padding: 0px;
    font-size: 20px;
}

.list__button {
    background-color: white;
    border: none;
    color: teal;
    padding: 15px;
    transition: 200ms;
    align-items: center;
    margin: 10px;
}

.list__button:hover {
    color: hsl(180, 100%, 30%);
    transition: 100ms;
}

.list__button:disabled {
    color: #aaa;
}

.list__button:disabled:hover {
    -webkit-transform: none;
            transform: none;
}

.list__tasks {
    display: flex;
    border-bottom: 2px solid #dad5d5;
    padding: 15px 30px;
    transition: 200ms;
    align-items: center;
}

.list__tasks--hidden {
    display: none;
}

.list__item {
    margin-left: 20px;
    flex-grow: 2;
    transition: 200ms;
}

.list__item:hover {
    -webkit-transform: scale(101%);
            transform: scale(101%);
    transition: 200ms;
}

.list__item--done {
    text-decoration: line-through;
    text-decoration-thickness: 2.5px;
}

.list__checkButton {
    background-color: green;
    color: green;
    border: none;
    padding: 10px;
    transition: 200ms;
}

.list__checkButton:hover {
    transition: 200ms;
    background-color: hsl(120, 100%, 30%);
}

.list__checkButton--true {
    background-color: green;
    color: white;
    border: none;
    padding: 10px;
    transition: 200ms;
}

.list__removeButton {
    background-color: red;
    color: white;
    border: none;
    padding: 10px;
    transition: 200ms;
}

.list__removeButton:hover {
    transition: 200ms;
    background-color: hsl(0, 100%, 65%);
}

.list__button {
    border: none;
    background-color: transparent;
    padding: 15px;
    transition: 200ms;
    font-size: large;
    font-weight: 700;
    margin: 10px;
}

.list__button:hover {
    color: hsl(180, 100%, 35%);
    transition: 100ms;
}

.list__button:disabled {
    color: grey;
}
.section__title {
    flex-grow: 1;
}

.headings {
    display: flex;
    flex-direction: column;
    background-color: white;
}

.section__header {
    display: flex;
    flex-grow: 1;
    border-bottom: solid 3px #eaeaea;
    padding-left: 20px;
}

@media (max-width: 767px) {
    .section__header {
        flex-direction: column;
    }
}

.container {
    background-color: transparent;
    padding: 50px;
    max-width: 900px;
    margin: 0 auto;
    word-break: break-word;
}
